.App {
  text-align: center;
  background-color: #f0f4f8;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-header {
  background-color: #282c34;
  color: white;
  padding: 20px;
  border-radius: 10px;
  width: 80%;
  max-width: 900px;
}

.dashboard-sections {
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
}

.dashboard-item {
  background-color: #fff;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 25%;
  text-align: center;
}

.dashboard-item h2 {
  color: #333;
}

button {
  margin-top: 10px;
  padding: 10px 20px;
  background-color: #6200ea;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #3700b3;
}
